@import "../common/syles/sass/variables.scss";
@import "../common/syles/sass/mixins";

.mainBlock {
  min-height: 100vh;
  background-color: $bgColor;
  display: flex;
  align-items: center;

  .mainContainer {
    @extend container;
    @include flex(row, space-around, center, wrap);

    .text {
      @include text($TitleFontFamily, 24px, 300px, $primaryColor, 1);

      p {
        color: $textColor;
      }
    }

    .photoContainer {
      width: 500px;
      position: relative;

      &::before {
        @include blackout(0.2)
      }

      .photo {
        width: 100%
      }
    }

  }
}







